h1,
h2,
h3,
h4,
h5 {
  font-family: 'adelle-sans';
  font-weight: 300; }

h1 {
  font-size: 11.089rem;
  line-height: 0.81; }

h2 {
  font-size: 6.854rem;
  line-height: 1.06; }

h3 {
  font-size: 4.236rem;
  line-height: 1.14; }

h4 {
  font-size: 2.618rem;
  line-height: 1.25;
  margin-bottom: 1rem; }

h5 {
  font-size: 1.618rem;
  line-height: 1.32; }

img {
  width: 100%; }

body {
  padding: 0;
  margin: 0;
  font-size: 16px;
  line-height: 1.5;
  position: relative;
  color: #283C64; }

.logo {
  max-width: 100px; }
